<template>
  <div id="ecart2">
      <b-containe fluid>
            <b-container id="ecart">
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                     <h5>Localisation</h5>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared><b-icon icon="geo"></b-icon></b-button>
                    </b-col>
                    <b-col cols="10"><b-input type="text" v-model="localisation.pays" placeholder="Pays" ></b-input></b-col>
                </b-row>                
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared><b-icon icon="fullscreen-exit"></b-icon></b-button>
                    </b-col>
                    <b-col cols="10"><b-input type="text" v-model="localisation.region" placeholder="Région"></b-input></b-col>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared><b-icon icon="geo-alt"></b-icon></b-button>
                    </b-col>
                    <b-col cols="10"><b-input type="text" v-model="localisation.departement" placeholder="Département"></b-input></b-col>
                </b-row>                                
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="2" >
                        <b-button variant="outline-white" disabled squared><b-icon icon="bullseye"></b-icon></b-button>
                    </b-col>
                    <b-col cols="10"><b-input type="text" v-model="localisation.arrondissement" placeholder="Arrondissement"></b-input></b-col>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="6" ><b-button block pill variant="success" @click="addLocalisation(localisation)">Enregistrer</b-button></b-col>
                    <b-col cols="6"><b-button block pill variant="info" @click="cancelLocalisation()">Annuler</b-button></b-col>
                </b-row>                
            </b-container>  
      </b-containe>  
  </div>
</template>

<script>
import moment from 'moment'

export default {
    name:'LocalisationEdit',
    data(){
        return {
            localisation: this.$store.getters.getDetailLocalisation
        }
    },

    methods: {
        addLocalisation(localisation){
             this.localisation.updatedAt= moment.now();
          
            //this.pays=moment(moment.now()).format('Do MMMM YYYY, h:mm:ss a');
            this.$store.commit('addLocalisation',localisation);
            this.$router.push("/localisation-list");
        },
        cancelLocalisation(){
            this.$store.commit('setDetailLocalisation',"null");
            this.$store.commit('back');
        }
    }
}
</script>

<style scoped lang="scss">
    #ecart2 {
        margin-top: 2%;
    }
    #ecart {
        margin-top: 5%;
    }
</style>